import React from 'react'
import { Jumbotron, Row, Col } from 'reactstrap';

import Mapa from './Mapa'
import Scroll from '../Scroll'

const Kontakt = ()=>
  (
    <Jumbotron id="kontakt">
        <Scroll where="start" />
        <h2> Kontakt </h2>
      <Row>
        <Col xs="5">
        <h4> Chcesz umwi si na wizyt? </h4>
        <p> Zadzwo od poniedziaku do pitku w godzinach od 16:00 do 21:00 pod numer telefonu +48 501 047 391 (gdy nie odbieram, to prosz o pozostawienie wiadomoci - oddzwoni).</p>
        <h4> Jak do trafi do gabinetu? </h4>
        <p> Przyjmuj w gabinecie na ul. Tomasza Zana 14, gmina Stare Babice, miejscowo Latchorzew.</p>
        </Col>
        <Col xs="auto">
          <Mapa />
        </Col>  
      </Row>
      </Jumbotron>
   )

export default Kontakt;


